<template>
    <div>

      <div>

        <div class="brilliantCommentTop">
            <span>{{vTitle}}</span>
            <div class="dottedLine"></div>
        </div>

        <div>
            <ul class="commentList">
               <!-- 一条信息 -->
               <li v-for="item in resData.list" :key="item.id" @click.stop="handleOut(item.id)">
                
                  <!-- 顶部信息 蓝方 -->
                  <div class="topInfo" v-if="item.standpoint==false">

                      <!-- 点赞 -->
                      <div class="isFavour">
                          <span :class='item.isLike?"icon-in_like_icon":"icon-in_like_border_icon"'></span>
                          <span class="favourNum"> {{item.likeNum ? item.likeNum:''}} </span>
                      </div>

                      <!-- 用户信息 -->
                      <div class="myselfInfo">
                          <div class="cons">
                               <img :src="item.userIcon" />
                          </div>
                          <p class="userName" >{{item.userName}}</p>
                          <p class="publishTime">{{item.time | getDateDiff }}</p>
                      </div> 

                  </div>
                   <!-- 顶部信息  红方 -->
                  <div class="topInfo" v-else >

                      <!-- 用户信息 -->
                      <div class="userInfo">
                          <div class="pros">
                                <img :src="item.userIcon" />
                          </div>
                          <p class="userName" >{{item.userName}}</p>
                          <p class="publishTime">{{item.time | getDateDiff }}</p>
                      </div> 
                      <!-- 点赞 -->
                      <div class="isFavour">
                          <span :class='item.isLike?"icon-in_like_icon":"icon-in_like_border_icon"'></span>
                          <span class="favourNum"> {{item.likeNum ? item.likeNum:''}} </span>
                      </div>

                  </div>

                  <!-- 评论框 -->
                  <div class="commentBox">
                      <!-- 引用 -->
                      <p v-if="item.quoteId" class="quote"><span class="user">{{item.quote[0].userName ? item.quote[0].userName :'' }}</span> : <span class="comment">{{item.quote[0].commentText}}</span></p>
                      <!-- 一级评论 -->
                      <p>{{item.commentText}}</p>
                      <!-- 一级评论中的图片 -->
                      <img v-if="item.commentTextImg" class="commentImg" :src="item.commentTextImg" />
                      <!-- 二级评论 -->
                      <div class="moreComment" v-if="item.childrenList && item.childrenList.length>1">
                          <ol class="moreCommentList" v-for=" citem in (item.childrenList?item.childrenList:[])" :key="citem.id">
                              <li>
                                 <!-- 回复 一级评论 -->
                                 <p v-if="!citem.parentId">
                                    <span class="user">{{citem.userName?citem.userName:''}}</span> : <span  class="comment">{{citem.commentText}} <i v-if="citem.commentTextImg" style="color:blue">查看图片</i></span>
                                 </p>
                                 <!-- 回复 二级评论(就是回复评论的评论) -->
                                 <p v-else>
                                    <span  class="user">{{citem.userName}} </span> 回复 
                                    <span class="user"> {{citem.parent.userName}} </span> : <span class="comment">{{citem.commentText}} <i v-if="citem.commentTextImg" style="color:blue">查看图片</i> </span>
                                 </p>
                              </li>
                          </ol>
                          <p class="totalReply"> 共 {{item.totalReply}} 条回复 ＞ </p>
                      </div>
                  </div>

               </li>
               
            </ul>
        </div>


        <!--全部加载完毕 -->
        <p v-if="loadOver" class='loadCommentOver'>已全部加载完毕</p>
      </div>

    </div>

</template>
<script>
export default {
  props: ["resData", "vTitle"],
  components: {},
  data() {
    return {
      loadOver: false
    };
  },
  filters: {
    getDateDiff: function(dateTimeStamp) {
      var result = "";
      var minute = 1000 * 60;
      var hour = minute * 60;
      var day = hour * 24;
      var halfamonth = day * 15;
      var month = day * 30;
      var now = new Date().getTime();
      var diffValue = now - dateTimeStamp;
      if (diffValue < 0) {
        return;
      }
      var monthC = diffValue / month;
      var weekC = diffValue / (7 * day);
      var dayC = diffValue / day;
      var hourC = diffValue / hour;
      var minC = diffValue / minute;
      if (monthC >= 1) {
        result = "" + parseInt(monthC) + "月前";
      } else if (weekC >= 1) {
        result = "" + parseInt(weekC) + "周前";
      } else if (dayC >= 1) {
        result = "" + parseInt(dayC) + "天前";
      } else if (hourC >= 1) {
        result = "" + parseInt(hourC) + "小时前";
      } else if (minC >= 1) {
        result = "" + parseInt(minC) + "分钟前";
      } else result = "刚刚";
      return result;
    }
  },
  methods: {
    loadOverFun() {
      this.loadOver = true;
    },
    // 最外层
    handleOut(id){
      console.log('out'+ id)
    },
    addCommentFun(moreComm) {
      moreComm.forEach(item => {
          this.resData.list.push(item)
      });
    }
  },

  mounted() {},
  created() {}
};
</script>
<style lang='less' scoped>
@m: 750/375;
.icon-in_like_icon {
  color: #ff8888;
  margin-right: 8px * @m;
}
.icon-in_like_border_icon {
  color: #c9d6de;
  margin-right: 8px * @m;
}
// 顶部
.brilliantCommentTop {
  position: relative;
  font-size: 14px * @m;
  color: #c6c6c6;
  text-align: center;
  margin-bottom: 20px * @m;
  margin-top: 30px * @m;
}
.brilliantCommentTop span {
  background-color: #fff !important;
  position: relative;
  z-index: 10;
  padding: 0 8px * @m;
}
.brilliantCommentTop .dottedLine {
  position: absolute;
  top: 50%;
  width: 100%;
  height: 1px * @m;
  border-top: 1px * @m dashed #c6c6c6;
}
// 评论框
.commentList li {
  margin-bottom: 30px * @m;
}
// 评论框顶部信息
.commentList li .topInfo {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px * @m;
}
.commentList li .userInfo {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
}
.commentList li .userInfo img {
  display: block;
  width: 40px * @m;
  height: 40px * @m;
  border-radius: 100%;
  margin-right: 10px * @m;
}
.commentList li .userInfo .userName {
  font-size: 14px * @m;
  color: #435771;
  margin-right: 6px * @m;
}
.commentList li .userInfo .publishTime {
  font-size: 12px * @m;
  color: #c6c6c6;
}
// 我自己的
.commentList li .myselfInfo {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: flex-start;
}
.commentList li .myselfInfo img {
  display: block;
  width: 40px * @m;
  height: 40px * @m;
  border-radius: 100%;
  margin-left: 10px * @m;
}
.commentList li .myselfInfo .userName {
  font-size: 14px * @m;
  color: #435771;
  margin-left: 6px * @m;
}
.commentList li .myselfInfo .publishTime {
  font-size: 12px * @m;
  color: #c6c6c6;
}

.commentList .topInfo .isFavour {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}
.commentList .topInfo .isFavour .favourNum {
  color: #c9d6de;
  font-size: 12px * @m;
}
//评论框
.commentList .commentBox {
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
  font-size: 16px * @m;
  box-sizing: border-box;
  padding: 16px * @m;
}
// 评论框的回复区域
.commentList .commentBox .moreComment {
  padding: 10px * @m;
  background: #f3f3f3;
  border-radius: 6px * @m;
  margin-top: 10px * @m;
}
.moreCommentList li {
  margin-bottom: 10px * @m;
  font-size: 14px * @m;
}

.moreComment .totalReply {
  color: #649be1 !important;
}
// 回复用户颜色
.user {
  color: #649be1;
}
//回复用户评论的颜色
.comment {
  color: #0f2540;
}
//回复用户发出的图片
.commentImg {
  display: block;
  width: 100px * @m;
  height: 100px * @m;
  border-radius: 8px * @m;
}
//引用回复
.quote {
  background: #f3f3f3;
  border-radius: 6px * @m;
  padding: 10px * @m;
  margin-bottom: 10px * @m;
}
.loadCommentOver {
  text-align: center;
  font-size: 13px * @m;
  color: #c6c6c6;
}

// PK阵营的
.pros {
  background: #d8d8d8;
  box-shadow: 0 5px * @m 15px * @m 0 rgba(252, 135, 135, 0.5);
  border-radius: 100px * @m;
  border: 2px * @m solid transparent;
  border-color: #ffb4b4;
  margin-right : 10px * @m;
}
.pros img{
    margin-right: 0 !important;
}
.cons {
  box-shadow: 0 5px * @m 15px * @m 0 rgba(91, 177, 253, 0.5);
  border-radius: 100px * @m;
  border: 2px * @m solid transparent;
  border-color: rgb(91, 177, 253);
  margin-left : 10px * @m;
}
.cons img {
  margin-left: 0 !important;
}
</style>


