<template>
    <div class="aboutTags">
        <p>相关标签</p>
        <ul class="aboutTagsList">
            <li v-for="(item,index) in resData" :key="index" @click="enterTag(item.tagId)">
                <span> # {{item.tagName}}</span>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  props: ["resData"],
  data() {
    return {};
  },
  methods: {
    enterTag(id) {
      alert(id);
    }
  },
  mounted() {}
};
</script>
<style lang='less' scoped>
@m: 750/375;
.aboutTags {
  box-sizing: border-box;
  font-size: 14px * @m;
  width: 100%;
  height: 60px * @m;
  margin-bottom: 30px * @m;
}
.aboutTags p:nth-child(1) {
  // font-size: 14px * @m;
  text-align: left;
  margin-bottom: 20px * @m;
}

.aboutTagsList {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  // margin-bottom: 30px * @m;
}

.aboutTagsList li {
  height: 26px * @m;
  background-color: #f5f7f9;
  border-radius: 100px * @m;
  border: 1px * @m solid #f5f7f9;
  box-sizing: border-box;
  margin-right: 10px * @m;
}

.aboutTagsList li span {
  line-height: 26px * @m;
  color: #9bc2d9;
  margin: 6px * @m 8px * @m;
}
</style>



