<template>
   <div>
        <ul class="activityList">
            <li v-for="(item,index) in resData " :key="index" @click="handleEnter(item.activityId)">
                <img :src="item.activityIcon">
                <div class="activityInfo">
                    <p class="activityName">{{item.activityName}}</p>
                    <p class="otherInfo"> {{item.time}} · {{item.activityFrom}}</p>
                </div>
            </li>
        </ul>
   </div>
</template>
<script>
export default {
  props: ["resData"],
  data() {
    return {};
  },
  methods: {
    handleEnter(id) {
      alert(id);
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang='less' scoped>
@m: 750/375;
.activityList {
  display: flex;
  flex-wrap: wrap;
}

.activityList li {
  margin-bottom: 20px * @m;
  width: 100%;
  height: 110px * @m;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
  display: flex;
  overflow: hidden;
}

.activityList li img {
  display: block;
  width: 165px * @m;
  height: 100%;
}

.activityList li .activityInfo {
  flex: 1;
  box-sizing: border-box;
  padding: 18px * @m 16px * @m;
}

.activityList li .activityName {
  font-size: 16px * @m;
  margin-bottom: 16px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.activityList li .otherInfo {
  font-size: 13px * @m;
  color: #c6c6c6;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>




