import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'
import activityForTailPkDetail from '@/view/activity/activity-for-tail-pk-detail.vue'; //尾巴PK详情页

Vue.config.productionTip = false
Vue.use(Router)

const router = new Router({
  // base: 'activity-for-tail-pk-detail.html',
  routes: [{
    path: '/',
    name: 'activityForTailPkDetail',
    component: activityForTailPkDetail
  }, ]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



